.footer {
    background-color: $background_dark_color;
    min-height: 200px;
    padding: 24px 0 36px 0;
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 25px;
    }
    &__descr {
        color: #fff;
        font-weight: 700;
        margin-left: 45px;
    }
    &__ml {
        margin-left: auto;
    }
    &__links {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #a8adb1;
        margin-top: 19px;
    }
    &__link {
        color: $main_color;
        text-decoration: underline;
        transition: .2s all;
        &:hover {
            color: $main_color;
            opacity: 0.85;
        }
    }
    hr {
        border-color: #677177;
    }
}